/* eslint-disable react/jsx-props-no-spreading */
import { useContext, useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
// import CommentIcon from '@mui/icons-material/Edit';
import CommentIcon from '@mui/icons-material/Delete';
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import {
  DragDropContext,
  Draggable,
  Droppable,
  DropResult,
} from 'react-beautiful-dnd';
import { useParams } from 'react-router-dom';
import { FirebaseContext } from '../firebase';

export default function Quest() {
  const { addItem, getChecklist, reorderSteps } = useContext(FirebaseContext);
  const [checked, setChecked] = useState([0]);
  const { id } = useParams();

  const [item, setItem] = useState('');
  const [data, setData] = useState([]);

  const handleSubmit = (e: any) => {
    e.preventDefault();
    addItem(Number(id), { title: item }, data.length);
    setItem('');
  };

  const handleChange = (e: any) => {
    if (e.key === 'Enter') {
      handleSubmit(e);
      return;
    }
    setItem(e.target.value);
  };

  useEffect(() => {
    const s = getChecklist(Number(id), setData);
    return () => s();
  }, [getChecklist, id]);

  const handleToggle = (value: number) => () => {
    const currentIndex = checked.indexOf(value);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const onDragEnd = ({ destination, source }: DropResult) => {
    // dropped outside the list
    if (!destination) return;
    console.log(destination, source, data);
    const d = [...data.slice(0, source.index), ...data.slice(source.index + 1)];
    const newData = [
      ...d.slice(0, destination.index),
      data[source.index],
      ...d.slice(destination.index),
    ];
    reorderSteps(Number(id), newData);

    // const newItems = reorder(items, source.index, destination.index);

    // setItems(newItems);
  };

  const onRemove = (i: number) => {
    reorderSteps(Number(id), [...data.slice(0, i), ...data.slice(i + 1)]);
  };

  return (
    <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="droppable-list">
          {(provided) => (
            <List
              sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}
              ref={provided.innerRef}
              {...provided.droppableProps}
            >
              {data.map(({ title }, index) => {
                const labelId = `checkbox-list-label-${title}`;

                return (
                  <Draggable draggableId={`${index}-${title}`} index={index} key={title}>
                    {(provided$) => (
                      <ListItem
                        ref={provided$.innerRef}
                        {...provided$.draggableProps}
                        {...provided$.dragHandleProps}
                        secondaryAction={
                          <IconButton
                            edge="end"
                            aria-label="comments"
                            onClick={() => onRemove(index)}
                          >
                            <CommentIcon />
                          </IconButton>
                        }
                        disablePadding
                      >
                        <ListItemButton
                          role={undefined}
                          onClick={handleToggle(title)}
                          dense
                        >
                          <ListItemIcon>
                            <Checkbox
                              edge="start"
                              checked={checked.indexOf(title) !== -1}
                              tabIndex={-1}
                              disableRipple
                              inputProps={{ 'aria-labelledby': labelId }}
                            />
                          </ListItemIcon>
                          <ListItemText id={labelId} primary={title} />
                        </ListItemButton>
                      </ListItem>
                    )}
                  </Draggable>
                );
              })}
              {provided.placeholder}
            </List>
          )}
        </Droppable>
      </DragDropContext>
      <ListItem>
        <form onSubmit={handleSubmit}>
          <Input value={item} onChange={handleChange} />
          <Button sx={{ fontWeight: 700 }} type="submit">
            Add new step
          </Button>
        </form>
      </ListItem>
    </Box>
  );
}
