import { initializeApp } from 'firebase/app';
import {
  // child,
  getDatabase,
  off,
  onValue,
  // push,
  ref,
  set,
  update,
} from 'firebase/database';
import { createContext, PropsWithChildren, useCallback } from 'react';
import config from './config';

interface Controller {
  addItem: (id: number, item: any, key: number) => void;
  addQuest: (item: any, key: number) => void;
  getQuests: (cb: any) => any;
  getChecklist: (id: number, cd: any) => any;
  reorderSteps: (id: number, newData: any) => void;
}
export const Context = createContext<Controller>({} as Controller);
const { Provider } = Context;

export default function FirebaseProvider({ children }: PropsWithChildren) {
  initializeApp(config);
  const db = getDatabase();
  // const checklistRef = ref(db, '/quests/0/steps');

  const getChecklist = useCallback(
   (id: number, cb: any) => {
      // return new Promise((resolve) => {
      const r = ref(db, `/quests/${id}/steps`);
      onValue(r, (snapshot) => {
        const data = snapshot.val();
        return data && cb(Object.values(data));
      });
      return () => off(r);
      // });
    },
    [db]
  );

  const getQuests = (cb: any) => {
    const r = ref(db, `/quests`);
    onValue(r, (snapshot) => {
      const data = snapshot.val();
      return cb(Object.values(data));
    });
    return () => off(r);
  };

  const reorderSteps = (id: number, newData: any) => {
    set(ref(db, `/quests/${id}/steps`), newData);
  };

  const addItem = (id: number, item: string, key: number) => {
    // const newPostKey = push(child(ref(db), '/quests/0/steps')).key;
    update(ref(db, `/quests/${id}/steps`), { [`/${key}`]: item });
  };

  const addQuest = (item: string, key: number) => {
    // const newPostKey = push(child(ref(db), '/quests/0/steps')).key;
    update(ref(db, `/quests`), { [`/${key}`]: item });
  };

  const controller = {
    addItem,
    getChecklist,
    reorderSteps,
    getQuests,
    addQuest,
  };
  return <Provider value={controller}>{children}</Provider>;
}
